.container {
    width: 100%;
    min-height: 40vh;
    background-color: darkred;
}
.iconBlock {
    width: 350px;
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border: 1px solid white;
    border-radius: 5px;
    align-items: center;
    margin: 0 auto;
    flex-wrap: wrap;
}

.iconsWrapper {
    display: flex;
    justify-content: space-between;
    width: 100%;
    min-height: 50px;
    border: 1px solid;
}
.icon {
    min-width: 50px;
    min-height: 50px;
    background-color: black;
}








