.remoteWork {
    display: flex;
    flex-direction: column;
    border: 1px solid blue;
    background-color: darksalmon;
}
.remoteWorkBlock {
    width: 100%;
    min-height: 30vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    border: 1px solid white;
    border-radius: 5px;
    align-items: center;
    margin: 0 auto;
    flex-wrap: wrap;
}

.button {
    height: 50px;
    width: 100px;
}