.project {
    max-width: 500px;
    border: 1px solid red;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.projectImg {
    max-width: 100%;
}
.button {
    height: 50px;
    width: 100px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%);
}
.wrapperBlock {
    justify-content: center;
    position: relative;
    border: 1px solid green;
}



