.container {
    width: 100%;
    height: 100%;
    min-height: 100vh;
    background-color: #02f3ab;
}
.formBlock {
    width: 80%;
    min-height: 60vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border: 1px solid white;
    border-radius: 5px;
    align-items: center;
    margin: 0 auto;
    flex-wrap: wrap;
}

.button {
    height: 50px;
    width: 100px;
}
.title {
    font-size: 60px;
    font-family: "Montserrat Thin", sans-serif;
    font-weight: bolder;
    text-transform: uppercase;
    letter-spacing: 0.125em;
    padding: 40px 0 40px 0;

}

.form {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
textarea {
    min-height: 150px;
}

@media (min-width: 860px) {
    .form {
        width: 600px;
    }
}

