.skillsBlock {
    min-height: 100vh;
    background-color: green;
}

.skillsContainer {
    flex-direction: column;
    justify-content: flex-start;

}

.title {
    background-color: bisque;
}

.skills {
    border: 1px solid ;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 20px;
}

@media (max-width: 860px) {
    .skills {
        justify-content: center;
    }
}

